<template>
  <div class="contact container">
    <transition name="slide-fade" appear>
      <div
        class="title"
        :style="{backgroundImage: 'url(' + require('../assets/img/' + 'contact_icon.png') + ')' }"
      >
        <p :class="$mq">작은 기회로 부터 종종 위대한 업적이 시작된다.</p>

        <button class="pop_Btn" @click="openModal">
          WRITE
          <img src="../assets/img/home_arr_w.png" alt />
        </button>
      </div>
    </transition>

    <transition name="fade">
      <modal-popup v-if="isShow">
        <form id="send-form">
          <input type="text" name="email" v-model="email" placeholder="보내는이 이메일" />
          <input type="text" name="subject" v-model="subject" placeholder="제목" />
          <textarea name="msg" placeholder="내용" v-model="msg"></textarea>
          <input type="submit" value="보내기" @click.prevent="formSubmit()" />
        </form>
      </modal-popup>
    </transition>

    <div class="contents" :class="$mq">
      <div class="info" :class="$mq">
        <h3>Contact</h3>
        <div class="info_list" :class="$mq">
          <ul>
            <li :class="$mq">
              <h4>기본정보</h4>
              <dl>
                <dt>이름</dt>
                <dd>이효준</dd>
              </dl>
              <dl>
                <dt>생년월일</dt>
                <dd>1980.01.26</dd>
              </dl>
              <dl>
                <dt>분야</dt>
                <dd>웹퍼블리싱, 프론트엔드, 기획디자인</dd>
              </dl>
            </li>

            <li :class="$mq">
              <h4>연락처</h4>
              <dl>
                <dt>휴대전화</dt>
                <dd><a href="tel:01036050325">010-3605-0325</a></dd>
              </dl>
              <dl>
                <dt>이메일</dt>
                <dd><a href="mailto:vanesco@naver.com">vanesco@naver.com</a></dd>
              </dl>
              <dl>
                <dt>주소</dt>
                <dd>경기도 남양주시</dd>
              </dl>
            </li>
          </ul>
        </div>
      </div>
    </div>

    <!-- <div class="admin">
      <button type="button" class="login" @click="openLogin">로그인</button>

      <div class="loginBox" v-if="loginSeen">
        <form id="login-form">
          <input type="text" name="userId" v-model="userId" placeholder="아이디" />
          <input type="password" name="password" v-model="password" placeholder="비밀번호" />
          <input type="submit" value="로그인" @click.prevent="login" />
          <button type="button" class="close" @click="closeLogin">닫기</button>
        </form>
      </div>
    </div>-->

    <!-- <div class="board-list" v-if="boardSeen">
      <ul>
        <li v-for="(list, index) in viewList" :key="index">
          <span class="index">{{index}}.</span>
          <span class="email">{{list.email}}</span>
