</template>

<script>
import { modalPopMixin } from "@/mixins/mixin";
import { EventBus } from "../main";

export default {
  name: "contact",
  mixins: [modalPopMixin],
  data() {
    return {
      userId: "",
      password: "",
      email: "",
      subject: "",
      msg: "",
      viewList: null,
      loginSeen: false,
      boardSeen: false
    };
  },
  mounted() {
    window.scrollTo(0, 0);
    // this.$http
    //   .get("/view")
    //   .then(response => {
    //     response.data.forEach(function(element) {
    //       let createdAt = new Date(element.createdAt);
    //       let date =
    //         createdAt.getFullYear() +
    //         "-" +
    //         get2digits(createdAt.getMonth() + 1) +
    //         "-" +
    //         get2digits(createdAt.getDate());
    //       let time =
    //         get2digits(createdAt.getHours()) +
    //         ":" +
    //         get2digits(createdAt.getMinutes()) +
    //         ":" +
    //         get2digits(createdAt.getSeconds());

    //       function get2digits(num) {
    //         return ("0" + num).slice(-2);
    //       }

    //       let created = date + " " + time;
    //       element.createdAt = created;
    //     });

    //     this.viewList = response.data;
    //   })
    //   .catch(err => {
    //     console.log(error);
    //   });
  },
  methods: {
    formSubmit() {
      let email = this.email;
      let subject = this.subject;
      let msg = this.msg;

      if (email == "" || subject == "" || msg == "") {
        if (email == "") {
          $("input[name=email]")
            .css({
              border: "1px solid #35a67d"
            })
            .attr("placeholder", "이메일을 입력하세요")
            .focus();
        } else if (subject == "") {
          $("input[name=subject]")
            .css({
              border: "1px solid #35a67d"
            })
            .attr("placeholder", "제목을 입력하세요")
            .focus();
        } else if (msg == "") {
          $("textarea")
            .css({
              border: "1px solid #35a67d"
            })
            .attr("placeholder", "내용을 입력하세요")
            .focus();
        }
      } else {
        if (confirm("보내시겠습니까?")) {
          this.$http
            .post("/write", {
              email: this.email,
              subject: this.subject,
              msg: this.msg
            })
            .then(function(response) {
              console.log(response);
            })
            .catch(function(error) {
              console.log(error);
            });

          alert("작성완료");

          this.$http
            .get("/view")
            .then(response => {
              response.data.forEach(function(element) {
                let createdAt = new Date(element.createdAt);
                let date =
                  createdAt.getFullYear() +
                  "-" +
                  get2digits(createdAt.getMonth() + 1) +
                  "-" +
                  get2digits(createdAt.getDate());
                let time =
                  get2digits(createdAt.getHours()) +
                  ":" +
                  get2digits(createdAt.getMinutes()) +
                  ":" +
                  get2digits(createdAt.getSeconds());

                function get2digits(num) {
                  return ("0" + num).slice(-2);
                }

                let created = date + " " + time;
                element.createdAt = created;
              });
              this.viewList = response.data;
            })
            .catch(err => {
              console.log(err);
            });

          this.email = "";
          this.subject = "";
          this.msg = "";
        }
      }
    }
    // focusEvent(event) {
    //   $(event.target).focusout(function() {
    //     $(event.target).css("border", "1px solid #222");
    //   });
    // },
    // CheckEmail(event) {
    //   $(event.target).focusout(function() {
    //     let reg_email = /^([0-9a-zA-Z_\.-]+)@([0-9a-zA-Z_-]+)(\.[0-9a-zA-Z_-]+){1,2}$/;
    //     let email = $(event.target).val();
    //     if (!reg_email.test(email)) {
    //       $(event.target).val("이메일 주소를 확인하세요.");
    //       return false;
    //     }
    //   });
    // },
    // openLogin() {
    //   this.loginSeen = true;
    // },
    // closeLogin() {
    //   this.loginSeen = false;
    // },
    // login() {
    //   this.$http
    //     .post("/login", {
    //       userId: this.userId,
    //       password: this.password
    //     })
    //     .then(response => {
    //       if (response.data != null) {
    //         this.boardSeen = true;
    //         this.loginSeen = false;
    //       } else {
    //         alert("권한이 없습니다.");
    //       }
    //     })
    //     .catch(err => {
    //       console.log(error);
    //     });
    // }
  },
  updated() {
    EventBus.$on("modalPopClose", isShow => {
      this.isShow = isShow;
    });
  }
};
</script>

<style lang="scss" scoped>
.title {
  margin: 7rem 0 0 0;
  padding: 15rem 0 6rem 0;
  text-align: center;
  font-size: 2rem;
  background-color: #35a67d;
  background-repeat: no-repeat;
  background-position: center 60px;
  color: #fff;
  p {
    font-size: 4rem;
    font-weight: 300;
    &.mobile {
      padding: 0 2rem;
      font-size: 3rem;
    }
    &.tablet {
      padding: 0 8rem;
    }
  }
  button {
    margin: 3rem 0 0 0;
    padding: 1.3rem 2.5rem;
    font-size: 1.4rem;
    color: #fff;
    border: 1px solid #fff;
    img {
      margin: 0 0 0 0.5rem;
      width: 0.7rem;
    }
  }
}

.contents {
  width: 70%;
  margin: 9rem auto 14rem auto;
  &.mobile {
    width: 90%;
    margin: 3rem auto;
  }
  &.tablet {
    width: 90%;
  }
  .info {
    margin: 0 0 5rem 0;
    &.mobile {
      margin: 0;
    }

    h3 {
      padding: 0 0 3rem 0;
      font-size: 5rem;
      font-weight: 400;
      color: #363636;
      text-align: center;
    }
    .info_list {
      font-size: 1.6rem;
      border-top: 3px solid #363636;
      &.mobile {
        border-top: none;
      }
      li {
        width: 50%;
        padding: 0 0 0 1.5rem;
        float: left;
        &.mobile {
          width: 100%;
          padding: 0;
          margin: 0 0 3rem 0;
          float: none;
          border-top: 2px solid #363636;
        }
        &.tablet dt {
          width: 9rem;
        }
        h4 {
          padding: 2rem 0;
          border-bottom: 1px solid #e1e1e1;
        }
        dl {
          display: table;
        }
        dt,
        dd {
          display: table-cell;
        }
        dt {
          width: 10rem;
          padding: 1rem 0;
          color: #363636;
        }
        dd {
          color: #868686;
          a:hover {
            color: #222;
            text-decoration: underline;
          }
        }
        &:first-child {
          padding: 0 1.5rem 0 0;
        }
      }
      &:after {
        content: "";
        display: block;
        clear: both;
      }
    }
  }
}

#send-form {
  width: 90%;
  margin: 0 auto;
  padding: 2rem 0;
  input[type="text"] {
    width: 100%;
    margin: 0 0 1.5rem 0;
    padding: 1.5rem 1rem;
    border: 1px solid #ddd;
    box-sizing: border-box;
    display: block;
  }
  textarea {
    width: 100%;
    height: 20rem;
    padding: 1rem;
    box-sizing: border-box;
    resize: none;
  }
  input[type="submit"] {
    width: 100%;
    margin: 3rem 0 0 0;
    padding: 1.5rem 2.5rem;
    font-size: 1.4rem;
    color: #fff;
    border: none;
    background: #35a67d;
    border-radius: 0.3rem;
    display: block;
  }
}

